//
// Multi select
//

.c-select{
  padding: 5px;
  border: 1px solid;

  /* variables */
  border-color: #aaa;
  border-radius: 3px;
}

.c-select > select{
  border: none;
  border-top: 1px solid #aaa;
  border-radius: 0;

  /* variables */
}
.c-select > select::after{
  display: none;

  /* variables */
}
.c-select > select.hide{
  display: none;
}

.c-select > select optgroup{
  /* variables */
}

.c-select > ul{
  max-height: 115px;
  padding: 0;
  margin-bottom: 0;
  overflow-y: scroll;
  background-color: #fff;
  border-top: 1px solid #aaa;

  /* variables */
  transition: max-height .2s;
}
.c-select > ul.hide{
  max-height: 0px;
  border-top: none;
}

.c-select > ul li{
  line-height: 25px;
  list-style-type: none;
  cursor: pointer;

  /* variables */
}
.c-select > ul li:hover{
  background-color: #d0d0d0;
}
.c-select > ul li.active{
  background-color: #d0d0d0;

  /* variables */
}
.c-select > ul li.c-label{
  /* variables */
  color: #b0b0b0;
}
.c-select > ul li.c-label:hover{
  /* variables */
  background: none;
}
.c-select > ul ul{
  padding-left: 20px;

  /* variables */
}

.c-tag-area{
  /* variables */
}

.c-tag{
  display: inline-block;
  min-width: 50px;
  min-height: 25px;
  padding: 1px 5px;
  margin: 2px;
  line-height: 25px;
  background-color: #e0e0e0;
  border: 1px solid;
  border-color: #d0d0d0;
  border-radius: 3px;
}

.c-tag > button{
  margin-top: 2px;
  margin-left: 2px;

  /* variables */
}

.c-select-search{
  margin: 5px 0;

  /* variables */
}

.c-select-search input {
  width: 100%;
  border: none;

  /* variables */
}

.c-select-search input:focus {
  outline: none;

  /* variables */
}
